import React,{useState} from 'react'

const FrontScreen = (props)=>{
    const [searchValue,setSearchValue] = useState('')
    const handleChange = (event)=>{
        setSearchValue(event.target.value)
    }
    // const states = props.dataList.map((item1,item2)=>{
    //     return item1.state
    // });
    // const filterStates = states.filter((item)=>{
    //     return item.toLowerCase().includes(searchValue.toLowerCase())
    // })
    const anotherList = props.dataList.filter((item,item2)=>{
       return item.state.toLowerCase().includes(searchValue.toLowerCase()) && item.state!=="Total"
    })
    console.log(anotherList)
    // console.log(states)

    return (
        <>
        <input type="text" placeholder="Search" value={searchValue} onChange={handleChange}/>
        <table class="table table-hover table-dark">
  <thead>
    <tr>
      <th scope="col">State</th>
      <th scope="col">Confirmed</th>
      <th scope="col">Recovered</th>
      <th scope="col">Deaths</th>
      <th scope="col">Active</th>
      <th scope="col">Last Updated</th>
    </tr>
   </thead>
                {
                    anotherList.map((item,item2)=>{
                        return (
                            <tr>
                             <td>{item.state}</td>
                             <td>{item.confirmed}</td>
                             <td>{item.recovered}</td>
                             <td>{item.deaths}</td>
                             <td>{item.active}</td>
                             <td>{item.lastupdatedtime}</td>
                            </tr>
                        )
                    })
                }
        </table>
        </>
    )
}

export default FrontScreen